import React, { useState, useEffect } from "react";
import BookmarkBorderIcon from "@material-ui/icons/BookmarkBorder";

const Works = () => {
  const repoUserName = "rakeshnayak360";
  const [arrayItems, setArrayItems] = useState([]);

  useEffect(() => {
    abc();
  }, []);

  const abc = () => {
    fetch(`https://api.github.com/users/${repoUserName}/repos`)
      .then((response) => response.json())
      .then((data) => {
        const items = data.map((arraySingleItem) => {
          return arraySingleItem;
        });
        console.log(items);
        setArrayItems(items);
      });
  };

  return (
    <div>
      <h1 className="title text-center">Works</h1>

      <div className="works text-left">
        {arrayItems.map((item, key) => {
          return (
            <div className="repo-card-div" key={key}>
              <a href="google.com" target="_blank" rel="noreferrer">
                <div className="repo-name-div">
                  <BookmarkBorderIcon />
                  <p className="repo-name">{item.name}</p>
                </div>

                <p className="repo-description">{item.description}</p>
                <div className="repo-stats">
                  <div className="repo-left-stat"></div>
                  <div className="repo-right-stat"></div>
                </div>
              </a>
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default Works;
